@import "headings";

@mixin copy {

  @include heading-sizes;

  b, strong {
    font-weight: bold; }

  em, i {
    font-style: italic; }

  p, ul, ol, pre, blockquote {
    &:not(:last-child) {
      margin-bottom: 1em; } }

  li {
    &:not(:last-child) {
      margin-bottom: 0.5em; } }

  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;

    &:not(:first-child) {
      margin-top: 1em; }

    h1 + &, h2 + &, h3 + &, h4 + &, h5 + &, h6 + & {
      margin-top: 0; }

    &:not(:last-child) {
      margin-bottom: 0.3em; } }

  ul, ol {
    padding-left: 2em; }

  ol li {
    list-style-type: decimal; }

  ul li {
    list-style-type: disc; }

  *:not(pre) > code {
    border-radius: 0.3em;
    padding-left:  0.2em;
    padding-right: 0.2em; }

  pre {
    font-size:     0.9em;
    overflow:      auto;
    white-space:   pre;
    border-radius: 0.3em;

    > code {
      padding: 1rem !important; } }

  blockquote, aside {
    padding-left: 3em;
    position:     relative;
    font-style:   italic;
    opacity:      0.9;

    &:before {
      position:   absolute;
      top:        0;
      left:       0;
      font-size:  4em;
      font-style: normal;
      opacity:    0.5; } }

  blockquote:before {
    content: '”'; }

  aside:before, .exclamation + blockquote:before {
    content: '!'; } }
