////------------------------------------------
.heroHeader {
  //------------------------------------------

  // ----- Layout -----
  position:     relative;

  // ----- Looks -----
  background-color: #242426;

  // ----- Content layout -----
  .heroHeader-map {
    position: absolute;
    left:     0;
    top:      0;
    width:    100%;
    height:   100%;
    z-index:  1; } }



///------------------------------------------
.heroHeader-content {
  //------------------------------------------

  // ----- Layout -----
  position:     relative;
  min-height:   300px;
  max-width:    $ita-max-width;
  margin-left:  auto;
  margin-right: auto;

  // ----- Looks -----
  background-color: #242426;

  &, a, h2 {
    color: white; }

  // ----- Content layout -----
  .heroHeader-title {
    position: relative;
    z-index:  2; }

  // ----- RWD -----
  $padding: 2rem;

  @include to(s) {
    padding:         $padding;
    display:         flex;
    flex-direction:  column;
    justify-content: space-between;
    text-align:      center;

    .heroHeader-title-lineBreak {
      display: none; }

    .heroHeader-title:first-child {
      margin-bottom: $padding; } }

  @include from(m) {
    .heroHeader-title {
      position:  absolute;
      top:       50%;
      transform: translate(0, -50%);

      &._left {
        left: $padding; }

      &._right {
        right: $padding;
        text-align: right; } } } }



////------------------------------------------
.heroHeader-title {
  //------------------------------------------

  // ----- Looks -----
  font-size: 40px;
  text-shadow: 0 0 6px black; }




////------------------------------------------
.heroHeader-map {
  //------------------------------------------

  // ----- Looks -----
  background-color: #242426;

  // ----- Content -----
  .leaflet-control-zoom {
    &:not(:hover) {
      opacity: 0.4; }
    a {
      color: black; } }

  svg .leaflet-interactive {
    cursor: default;
    cursor: -webkit-grab; } }

